@import "https://fonts.googleapis.com/css2?family=Montserrat&display=swap";
body {
  background-color: #d3d3d3;
  font-family: Montserrat, sans-serif;
}

a {
  color: #2e2f42;
  margin-bottom: 50px;
  font-size: 20px;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

a:hover {
  color: #f08080;
}

.container {
  background-color: #fff;
  border-radius: 8px;
  width: 480px;
  margin: 0 auto;
  padding: 40px;
}

.wrapper {
  text-align: center;
}

#datetime-picker {
  letter-spacing: .04em;
  text-align: center;
  border: 1px solid silver;
  border-radius: 4px;
  padding: 10px;
  font-family: Montserrat, sans-serif;
  font-weight: 600;
}

#datetime-picker:disabled {
  cursor: auto;
  background-color: #d3d3d3;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4e75ff;
  border: none;
  border-radius: 8px;
  min-width: 95px;
  height: 40px;
  margin-top: 16px;
  padding-block: 8px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

button:disabled {
  cursor: auto;
  background-color: #6c8cff;
}

button:hover:not(:disabled), button:focus:not(:disabled) {
  background-color: #6c8cff;
}

span {
  display: block;
}

.timer {
  justify-content: center;
  gap: 16px;
  margin-top: 20px;
  display: flex;
}

.field {
  text-align: center;
}

.value {
  text-transform: uppercase;
  letter-spacing: .04em;
  font-size: 35px;
  font-weight: bold;
}

.label {
  text-transform: uppercase;
  letter-spacing: -.02em;
  font-weight: bold;
}
